/* 样式重置 */
html,body,ul,li,img,a,p,div {
    padding: 0;
    margin: 0;
    /* 设置盒模型 */
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    /* 去除移动端特有的点击高亮效果 */
    -webkit-tap-highlight-color: transparent;
}
body {
    font-family: '\5FAE\8F6F\96C5\9ED1',sans-serif;
    font-size: 13px;
}
/*清除a标签默认的下划线和链接颜色 */
a,a:hover {
    color: #666;
    text-decoration: none;
}
ul {
    list-style: none;
}
input {
    /* 清除文本框获取焦点时默认的边框阴影 */
    outline: none;
    /* 去除边框 */
    border: none;
    /* 添加边框 */
    border: 1px solid #ccc;
}
.clearfix::before,
.clearfix::after {
    content: "";
    display: block;
    clear: both;

}